@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1em;
    height: 3.5625em;
    border-radius: 0.6875em;
    &:focus {
        box-shadow: none;
    }
}

.m-btn-black{
    border: 1px solid $black;
    background: $black;
    box-shadow: 0 .375em .3125em 0 rgba(53, 35, 255, 0.25);
    color: #fff;
    font-weight: 700;
    span{
        font-size: 1em;
        line-height: 150%;
    }
    &:hover{
        background: lighten($black,10%);
        color: #fff;
        box-shadow: 0 .625em .5em 0 rgba(53, 35, 255, 0.25);
    }
}
