.header{
  padding: 2.75em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__logo{
    display: block;
    margin-right: auto;
    width: 9.8125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__phone{
    font-family: $font3;
    font-size: 1em;
    font-weight: 600;
    white-space: nowrap;
    margin-left: 1em;
  }
  &._open{
    .menu{
      opacity: 1;
      pointer-events: unset;
      &__bg{
        display: block;
      }
    }
  }
}

.menu__close,
.burger{
  @include center;
  &:hover{
    rect{
      fill: $primary;
    }
  }
}

.burger{
  width: 2.0625em;
  flex-shrink: 0;
  margin-left: 1.75em;
  svg{
    width: 100%;
    height: auto;
  }
}

.menu{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  right: 0;
  height: $fullHeight;
  overflow-y: auto;
  width: 17em;
  background-color: #fff;
  z-index: 11;
  display: flex;
  flex-direction: column;
  &__bg{
    display: none;
    z-index: 10;
    position: absolute;
    top: 0;
    right: 0;
    width: 100%;
    height: $fullHeight;
    background: rgba(0, 0, 0, 0.56);
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 3.44em 2.13em 2.62em 1.56em;
  }
  &__close{
    width: 1.125em;
    svg{
      width: 100%;
      height: auto;
    }
  }
  &__links{
    border-top: 1px solid #E0E0E0;
  }
  &__btn{
    margin: 2em auto 0;
    width: calc(100% - 3.92em);
  }
  .socials{
    margin-top: auto;
    padding: 2em 0;
    justify-content: center;
  }
}

.menu-item{
  &._active{
    .menu-item__show{
      svg{
        transform: rotate(-180deg);
      }
    }
  }
  &__sublink,
  &__show,
  &__link{
    min-height: 3.125em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .5em 1.81em;
    border-bottom: 1px solid #E0E0E0;
    transition: all .3s;
    &:hover{
      color: $primary;
      cursor: pointer;
      line{
        stroke: $primary;
      }
    }
    svg{
      width: .8125em;
      height: auto;
    }
  }
  &__hidden{
    background: #ECECEC;
    display: none;
  }
  &__sublink{
    &:last-child{
      border-bottom: none;
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    width: 4em;
    @include lastM(0 .625em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      ._hover-fill{
        fill: $black;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 2.5em 0;
    &__logo {
      width: 9.3125em;
    }
  }

  .burger{
    margin-left: 1.4em;
  }
}
