@import 'vars';
html {
    width: 100%;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    width: 100%;
}

.m-page {
    transition: all .3s;
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 300;
    color: $black;
    font-size: calc(1280px * (16 / 1280));
    @media screen and (max-width: 1280px){
        font-size: calc(100vw * (16 / 1280));
    }
    &.fixed-body,
    &.fixed-body2{
        overflow-y: hidden;
    }
    &.fixed-body2{
        padding-right: var(--scrollWidth);
    }
}

.container {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    padding: 0 4.125em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .m-page{
        font-size: calc(100vw * (16 / 768));
    }

    .container{
        padding: 0 2em;
        &._mob-full{
            padding: 0;
        }
    }
}


@media screen and (max-width: 576px){
    .m-page{
        font-size: calc(100vw * (16 / 390));
    }
}


@media screen and (max-width: 390px){
    .m-page{
        font-size: calc(100vw * (16 / 320));
    }

    .container{
        padding: 0 1em;
    }
}
